.header {
    grid-area: h;
    background-color:#242526;
}
.header img {
    width: 45px;
}

.photoUser {
    border-radius: 50%;
    margin-right: 10px;
}

.logo {
    margin: 9px 0 7px 90px;
    -ms-user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
}

.loginBlock {
    float: right;
    font-size: 20px;
    margin-right:  40px;
    margin-top: 13px;
    color: #fff;
}

.logout {
    font-size: 16px;
    outline: none;
    border: none;
    color: #fff;
    background-color: #434344;
    text-decoration: none;
    font-weight: bold;
    padding: 10px 10px;
    border-radius: 10px;
}

.logout:hover {
    background-color:  #4e4e4e;
}

.nav:hover {
    color: #bfb9bf;
}

.button {
    display: flex;
    width: 120px;
    height: 40px;
    text-align: center;
    border-radius: 5px;
    color: #fff;
    padding-right: 8px;
    font-size: 16px;
    justify-content: space-around;
    align-items: center;
    cursor: pointer;
    margin-top: 2px;
    -ms-user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
}

.button span {
    padding-bottom: 7px;
}

.button img {
    width: 30px;
    height: 30px;
}

.button:hover {
    background: #4c4c4c;
}

.button:focus {
    outline: none;
    box-shadow: 0 0 0 1px #37374a;
}

.isAuth {
    display: flex;

}

.name {
    margin-top: 3px;
    margin-right: 20px;
    font-size: 24px;
    font-family: Сomic Sans MS;
}