::-webkit-scrollbar { /* chrome based */
    width: 0px;  /* ширина scrollbar'a */
    background: transparent;  /* опционально */
}
html {
    -ms-overflow-style: none;  /* IE 10+ */
    scrollbar-width: none; /* Firefox */
}

.allWiew {
    margin-left: 15%;
    width: 70%;
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.dialogs {
    display: flex;
    justify-content: center;
    color: #fff;
    width: 60%;
    background-color: #242526;
    margin-bottom: 3px;
    border-radius: 15px;
    text-decoration:none;
    overflow-y: auto;
    height: 600px;
}

.dialogsElemnts {
    float: right;
}

.dialogMain {
    color: #fff;
    text-decoration: none;
    font-size: 22px;
}

.dialogMain:hover {
    text-decoration: none;
}

h1 {
    color: #fff;
}

.dialogsItems {
    padding: 20px 0 25px 0; 
}

.dialogChat {
    display: flex;
    margin: 20px 0 0 0;
    justify-content: center;
    font-size: 22px;
}

.dialogChat a {
    text-decoration: none;
    color: black;
    padding-top: 15px;

}

.dialogChat img {
    width: 50px;
    border-radius: 100%;
    margin-right: 20px;
}

.chatDialog {
    text-align: center;
    display: flex;
    justify-content: center;
    color: #fff;
    border: 2px solid #242526;
    width: 40%;
    background-color: #242526;
    border-radius: 10px;
    text-decoration:none;
    padding: 20px 0 20px 0;
    align-items: center;
    margin-bottom: 5px;
}

.dialog { 
    display: flex;
    margin: 20px 0 20px 0;
    padding-bottom: 5px;
    background-color: rgb(56, 56, 56);
    border-radius: 10px;
    padding: 7px 60px;
    align-items: center;
}

.dialog a {
    text-decoration: none;
    color: black;
    padding-top: 15px;

}

.dialog img {
    width: 50px;
    border-radius: 100%;
    margin-right: 20px;
}

.messages {
    padding-top: 26px;
    padding-left: 30px;
    margin-bottom: 100px;
    

}

.text {
    display: flex;
    padding: 0 0 30px 0;
    color: #fff;

}

.textarea {
    width: 500px;
}



.input {
    margin-right: 20px;
    margin-bottom: 100px;
}

.chatBlock {
    display: flex;
    justify-content: center;
}

.chat {
    height: 500px;
    width: 40%;
    overflow-y: auto;
    border: 2px solid #242526;
    background-color: #242526;
    border-radius: 10px;
}

.indent {
    margin-right: 20px;
}
.messagesBody {
    margin-left: 20px;
    margin-top: 20px;
}

.userName {
    width: 60px;
}

.userMessage {
    margin-left: 20px;
}

.messageBlock {
    display: flex;
    justify-content: center;
}

.formMessage {
    display: flex;
    margin-top: 20px;
}

.formMessage input {
    width: 385px;
    border-radius: 5px;
    padding: 3px 0 3px 20px; 
    margin-right: 10px;
}

.button {
    margin-top: 1px;
    padding: 2px 10px 3px 10px;
    border: 2px solid #007bff;
    text-decoration: none;
    border-radius: 5px;
    color: #fff;
    background-color: #007bff;
}







